
import {
  IonPage,
  IonContent,
  IonToolbar,
  IonHeader,
  IonTitle,
  IonFooter,
  IonButton,
  IonCol,
  IonRow,
  IonGrid,
  IonCard,
  IonItem,
  IonLabel,
  IonList,
} from "@ionic/vue";
import { Client } from "@/composables/client";
import { defineComponent } from "vue";
import moment from "moment";
import PrintService from "@/services/print_service";
import router from "@/router";

export default defineComponent({
  components: {
    IonPage,
    IonContent,
    IonToolbar,
    IonHeader,
    IonTitle,
    IonFooter,
    IonButton,
    IonCol,
    IonRow,
    IonGrid,
    IonCard,
    IonItem,
    IonLabel,
    IonList,
  },
  data() {
    return {
      details: {
        client: {
          first_name: "",
          middle_name: "",
          last_name: "",
          date_of_birth: "",
          sex: "",
        },
      } as any,
      requestedBy: "",
      receiptDate: "",
      moment: moment,
    };
  },
  async mounted() {
    const client = Client();

    const response = await client.getJson(
      `tests/${this.$router.currentRoute.value.query.test_id?.toString()}`
    );

    if (response?.ok) {
      this.details = response?.data;

      this.details.status_trail.map((trail: any) => {
        if (trail.status.name == "pending") {
          this.requestedBy = `${trail.initiator.first_name} ${trail.initiator.last_name}`;
        } else if (trail.status.name == "accepted") {
          this.receiptDate = trail.created_date;
        }
      });

      this.details.order_status_trail.map((trail: any) => {
        if (trail.status.name == "specimen-accepted") {
          this.receiptDate = trail.created_date;
        }
      });
    } else {
      console.log(response?.error);
    }
  },
  methods: {
    goBack() {
      router.back();
    },
    navigateToPatient() {
      router.push(`/patient-details/${this.details.client.id}`);
    },
    printAccessNumber() {
      const printService = new PrintService();

      const accession_number = this.details.accession_number;

      printService.writeLbl(
        "printout/accession_number",
        { accession_number },
        `${accession_number}.lbl`
      );
    },
  },
});
